                        >
                            <v-badge overlap color="error lighten-3" v-model="shownotification">
                                <template v-slot:badge>{{ totalnotificaciones }}</template>
                                 <v-icon >{{ $store.getters.getNotificacion }}</v-icon>
                            </v-badge>
                        </v-btn>
                    </div>
                </template>
            </v-menu>

            <v-menu
                medium
                icon
                v-model="menu"
                :close-on-content-click="false"
                :nudge-width="270"
                :nudge-top="15"
                offset-y
                >
                <template v-slot:activator="{ on }">
                    <div class="my-2">
                        <v-btn
                            v-on="on"
                            icon
                            slot="activator"
                            v-show="$session.get('logged')"
                        >
                            <v-icon>person</v-icon>
                        </v-btn>
                    </div>
                </template>

                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card color="lighten-3" >
                            <v-layout style="padding:10px">
                            <v-flex xs5 >
                                <v-hover>
                                    <v-layout justify-center>
                                        <v-avatar
                                            :tile="false"
                                            :size="100"
                                            contain
                                        >
                                        <v-img :src="require(`../../assets/img/${$store.getters.getAvatar}`)"></v-img>
                                        </v-avatar>
                                    </v-layout>
                                </v-hover>
                            </v-flex>

                            <v-flex xs7 style="padding:0px">
                                <v-layout align-center fill-height>
                                <v-card-title primary-title style="padding:0px">
                                    <div>
                                        <div class="tittle font-weight-bold" style="font-size:16px">
                                            {{ nombrecorto }}
                                        </div>
                                        <div style="font-size:12px;color:black">@{{$session.get('usuario')}}
                                        </div>
                                        <!--<div>
                                        <v-btn small style="margin-left:0px" @click="editPass(), menu = false" >
                                            Cambiar Contraseña
                                        </v-btn>
                                        </div>-->
                                        <div>
                                            <v-tooltip top>
                                                <template v-slot:activator="{ on }">
                                                    <v-btn class="mx-2" fab dark small color="primary" v-on="on" @click="editPass(), menu = false" >
                                                        <v-icon dark>vpn_key</v-icon>
                                                    </v-btn>
                                                </template>
