                                            </v-tooltip>

                                            <v-tooltip top>
                                                <template v-slot:activator="{ on }">
                                                    <v-btn class="mx-2" fab dark small color="primary" v-on="on" @click="editAlertas(), menu = false" >
                                                        <v-icon dark>add_alert</v-icon>
                                                    </v-btn>
                                                </template>
                                                <span>Configurar Notificaciones</span>
                                            </v-tooltip>

                                        </div>
