                        </v-card>
                    </v-flex>
                </v-layout>
            </v-menu>

            <div class="my-2">
                <v-tooltip bottom v-show="$session.get('logged')">
                    <template v-slot:activator="{ on }">
                        <v-btn icon medium v-on="on"  @click="cerrarsesion">
                            <v-icon>exit_to_app</v-icon>
                        </v-btn>
                    </template>
                    <span>Cerrar sesión</span>
                </v-tooltip>
            </div>

        </v-toolbar-items>
        </v-app-bar>

         <v-navigation-drawer v-model="drawer" width="300"
            app
