        <configura-alertas :configura_alertas.sync = "configura_alertas"></configura-alertas>

        <v-dialog v-model="dialog_notificaciones" max-width="1200px">
            <v-card color="lighten-3" >
                <v-btn
                    color ="primary"
                    class="mt-2 ml-5"
                    text
